body {
  background: #F7F3F2 !important;
  font-family: "Roboto", sans-serif !important;
  font-size: 12.8px !important;
  color: #817D7E !important; }
  body a {
    color: #005FAA !important;
    font-size: 1.1rem !important; }
    body a:hover {
      color: #029ABF !important;
      text-decoration: none !important; }

.logo {
  margin: 20px 10px; }

.topo {
  background-color: #fff !important; }

.slide, footer {
  background: #0080ba;
  /* Old browsers */
  background: -moz-linear-gradient(left, #0080ba 0%, #01a6de 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(left, #0080ba 0%, #01a6de 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to right, #0080ba 0%, #01a6de 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#0080ba', endColorstr='#01a6de',GradientType=1 );
  /* IE6-9 */ }

.imgSlide {
  float: right; }

.textSlide {
  text-align: left;
  top: 110px !important; }
  .textSlide h3 {
    font-size: 2.8rem; }
  .textSlide p {
    font-size: 1.2rem; }

.busca_cursos span {
  display: block;
  margin-bottom: 10px; }

.h2_destaque_cursos {
  color: #fff;
  height: 35px;
  margin-left: 25px;
  margin-bottom: 10px;
  margin-top: 0px;
  line-height: 35px;
  padding: 0 5px;
  font-size: 1.1rem;
  background: -moz-linear-gradient(left, #0082af 0%, #1f91b9 12%, rgba(146, 201, 221, 0) 350px, rgba(255, 255, 255, 0) 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(left, #0082af 0%, #1f91b9 12%, rgba(146, 201, 221, 0) 350px, rgba(255, 255, 255, 0) 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to right, #0082af 0%, #1f91b9 12%, rgba(146, 201, 221, 0) 350px, rgba(255, 255, 255, 0) 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#0082af', endColorstr='#00ffffff',GradientType=1 );
  /* IE6-9 */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#1e5799', endColorstr='#00ffffff',GradientType=1 );
  /* IE6-9 */ }
  .h2_destaque_cursos:before {
    content: "";
    position: absolute;
    height: 35px;
    width: 15px;
    background: #0082af;
    margin-left: -30px; }

.topologado {
  margin: 10px 0;
  border-top: 1px solid #E1E3E5;
  border-bottom: 1px solid #E1E3E5;
  background: #fff; }

.inputbuscacursos {
  border: 1px solid #e9e9e8;
  height: 35px;
  width: 100% !important;
  padding: 0 5px;
  line-height: 35px;
  border-radius: 5px;
  color: #0081AF; }

.h2_destaque {
  color: #fff;
  height: 35px;
  margin-left: 25px;
  margin-bottom: 30px;
  margin-top: 30px;
  line-height: 35px;
  padding: 0 5px;
  font-size: 1.1rem;
  background: -moz-linear-gradient(left, #0082af 0%, #1f91b9 12%, rgba(146, 201, 221, 0) 350px, rgba(255, 255, 255, 0) 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(left, #0082af 0%, #1f91b9 12%, rgba(146, 201, 221, 0) 350px, rgba(255, 255, 255, 0) 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to right, #0082af 0%, #1f91b9 12%, rgba(146, 201, 221, 0) 350px, rgba(255, 255, 255, 0) 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#0082af', endColorstr='#00ffffff',GradientType=1 );
  /* IE6-9 */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#1e5799', endColorstr='#00ffffff',GradientType=1 );
  /* IE6-9 */ }
  .h2_destaque:before {
    content: "";
    position: absolute;
    height: 35px;
    width: 15px;
    background: #0082af;
    margin-left: -30px; }

.porEstados {
  background-color: #FF7D0B;
  display: block;
  padding: 0 5px;
  line-height: 30px;
  height: 30px;
  margin-top: 3px;
  color: #fff;
  position: relative;
  margin-right: 40px; }
  .porEstados::before {
    content: '';
    content: '';
    width: 0;
    height: 0;
    border-top: 15px solid transparent;
    border-left: 20px solid #FF7D0B;
    border-bottom: 15px solid transparent;
    left: 77px;
    position: absolute; }

.grupoEstados {
  display: flex;
  font-size: 14px; }

.caixaestado {
  padding: 3px;
  background-color: #fff;
  margin: 3px;
  min-width: 31px;
  text-align: center;
  color: #FF7D0B;
  font-weight: 600;
  cursor: pointer; }
  .caixaestado:hover {
    background: #FF7D0B;
    color: #fff;
    transition: 0.5s; }

.destaques_cartao {
  flex-direction: row !important;
  align-items: center;
  margin-bottom: 15px; }
  .destaques_cartao img {
    width: 120px;
    height: 120px;
    margin-right: 10px; }

.menucaixa {
  width: 90px;
  height: 90px;
  text-align: center;
  margin: 20px 0px;
  cursor: pointer;
  background: #c8c8c8;
  /* Old browsers */
  padding: 20px 10px;
  color: #fff;
  text-transform: uppercase;
  box-sizing: border-box;
  font-size: 0.6rem;
  text-align: center; }
  .menucaixa i {
    display: block;
    color: #fff;
    font-size: 2.2rem;
    margin-bottom: 10px; }

.menusuperior {
  display: flex;
  flex-direction: row; }

.body_destaque {
  padding: 0 !important; }

.itemProva {
  background: #fff !important;
  border-left: #DCB53C 14px solid !important;
  margin-bottom: 10px !important;
  padding: 2px 5px; }
  .itemProva a {
    color: #767676 !important;
    font-size: 1rem !important; }

@media screen and (max-width: 480px) {
  .grupoEstados {
    flex-wrap: wrap; }
  .imgSlide {
    visibility: hidden; } }

.rodape {
  margin-top: 20px; }
